import React from 'react';
import { Spinner, Button } from 'reactstrap';

export const LoadingButton = ({
    loading = false,
    loadingProps = {
        color: "light",
        size: "sm"
    },
    children,
    ...props
}) => {
    return (
        <Button 
            disabled={loading}
            {...props}
        >
            {loading ?
            <Spinner {...loadingProps} /> : 
            children}
        </Button>
    )
}
