import React from 'react';
import CKEditor from 'ckeditor4-react';
import { useTranslation } from 'react-i18next';

export function RenderCKeditor({
    input,
    meta: { error, touched },
    label = null,
    hint = null,
    after = null,
    className = null,
    ...field
}) {
    const { i18n } = useTranslation();
    const isValid = !(error && touched);
    const errorClass = !isValid ? 'invalid' : '';
    const groupClass = className ? className : '';

    return (
        <div className={`form-group ${groupClass} ${errorClass}`}>
            {label &&
                <label className="form-label">{label}</label>}
            {hint && <div className="form-hint">{hint}</div>}
            {after && <div className="form-after">{after}</div>}

            <CKEditor
                data={input.value || ''}
                type="classic"
                config={{
                    language: i18n.language,
                    contentsCss: '/editor.css',
                    extraPlugins: 'colorbutton',
                    disableNativeSpellChecker: false,
                    toolbar: [[
                        'Bold',
                        'Italic',
                        'NumberedList',
                        'BulletedList',
                        'HorizontalRule',
                        'Format',
                        'Styles',
                        'Link',
                        'Image',
                        'Undo',
                        'Redo',
                        'Maximize'
                    ]]
                }}
                name={input.name}
                // {...field}
                onChange={(e) => input.onChange(e.editor.getData())}
            />
            {!isValid &&
                <div className="form-error">{error}</div>}
        </div>
    )
}