import React from 'react';
import Select from 'react-select';
import CreatableSelect from 'react-select/creatable';
import { useTranslation } from 'react-i18next';

export function RenderSelect({ 
    input, 
    meta: { error, touched, initial }, 
    label = null,
    hint = null,
    after = null,
    styles = {},
    className = null,
    ...field
}) {
    const isValid = !(error && touched);
    const errorClass = !isValid ? 'invalid' : '';
    const selectClass = className ? className : '';    
    const customStyle = styles;
    const { t } = useTranslation();
    const ForsaSelect = field.isCreatable ? CreatableSelect : Select;    
    
    const transformedValue = transformValue(input.value, field.options, field.isMulti, field.isCreatable);
    
    return (
        <div className={`form-group ${selectClass} ${errorClass}`}>
            {label &&
            <label className="form-label">{label}</label>}
            {hint && <div className="form-hint">{hint}</div>}            
            <ForsaSelect
                placeholder={t('select')}
                {...input}
                {...field}
                id={input.name}
                value={transformedValue}
                onChange={field.isMulti
                    ? multiChangeHandler(input.onChange)
                    : singleChangeHandler(input.onChange)
                }
                onBlur={event => event.preventDefault()}
                styles={customStyle}
                className='react-select'
                classNamePrefix='react-select'
            />
            {after &&
            <div className="form-after">{after}</div>}
            {!isValid &&
            <div className="form-error">{error}</div>}
        </div>
    )
}

/**
 * onChange from Redux Form Field has to be called explicity.
 */
function singleChangeHandler(func) {
    return function handleSingleChange(value) {
      func(value ? value.value : '');
    };
}

/**
 * onBlur from Redux Form Field has to be called explicity.
 */
function multiChangeHandler(func) {
    return function handleMultiHandler(values) {
      if(values) func(values.map(value => value.value));
      else func([]);
    };
}

// function creatableHandler(func) {
//     return function handleCreatable(values) {
//         let obj = {};
//         func(values.map(item => Object.assign(obj, { label: item, value: item })))
//     }
// }

/**
 * For single select, Redux Form keeps the value as a string, while React Select 
 * wants the value in the form { value: "grape", label: "Grape" }
 * 
 * * For multi select, Redux Form keeps the value as array of strings, while React Select 
 * wants the array of values in the form [{ value: "grape", label: "Grape" }]
 */
function transformValue(value, options, multi, creatable) {
    if (multi && typeof value === 'string') return [];
    
    const filteredOptions = options && options.filter(option => {        
        return multi 
        ? value.indexOf(option.value) !== -1
        : option.value === value;
    });

    return creatable ? value.map(item => ({ label: item, value: item })) : filteredOptions ? multi ? filteredOptions : filteredOptions[0] : null;
}