import React from 'react';

export function RenderInput({ 
    input, 
    shape = null,
    hint = null,
    label = null,
    className = null,
    after = null,
    meta: { error, touched }, 
    ...field
}) {
    const isValid = !(error && touched);
    const errorClass = !isValid ? 'invalid' : '';
    const inputClass = className ? className : '';

    return (
        <div className={`form-group ${inputClass} ${errorClass}`}>
            {label &&
            <label className="form-label">{label}</label>}
            {hint &&
            <div className="form-hint">{hint}</div>}
            <input 
                className="form-control"
                {...input} 
                {...field} 
            />
            {after &&
            <div className="form-after">{after}</div>}
            {!isValid &&
            <div className="form-error">{error}</div>}
        </div>
    )
}

export function RenderInputIcon({ 
    input, 
    shape = null,
    hint = null,
    label = null,
    icon = null,
    className = null,
    after = null,
    meta: { error, touched }, 
    ...field
}) {
    const isValid = !(error && touched);
    const errorClass = !isValid ? 'invalid' : '';
    const inputClass = className ? className : '';

    return (
        <div className={`input-group mb-2 ${inputClass} ${errorClass}`}>
            {label && <label className="sr-only" for="inlineFormInputGroup">{label}</label>}
            {icon && <div className="input-group-prepend">
                <div className="input-group-text">{icon}</div>
            </div>}
            <input 
                className="form-control"
                {...input} 
                {...field} 
            />
            {!isValid &&
            <div className="form-error">{error}</div>}
        </div>
    )
}

