import React, { useEffect } from 'react';
import cogoToast from 'cogo-toast';

export const FloatToast = ({
    message = 'Data loaded successfuly',
    header = 'Success',
}) => {

    useEffect(() => {
        cogoToast.success(message, {
            heading: header,
            position: "top-right"
        })
    }, [message, header]);

    return <></>;
}
