import Axios from './Config';
import * as endpoints from './Endpoints';
import qs from 'qs';

/**
 * 
 * Users
 */

export const getAppUsers = (callback) => {
    callback(true, [], null, false);
    return Axios.get(endpoints.appUsers)
    .then(res => {
        callback(false, [], null, false);
        if(res.status === 200) {
            callback(false, res.data, null, true);
        } else {
            callback(false, [], res, false);
        }
    }, err => {
        callback(false, [], err, false);
    }).catch(err => {
        callback(false, [], err, false);
    })
}

