import React from 'react';
// import { useTranslation } from 'react-i18next';

export function RenderFile({
    input,
    shape = null,
    hint = null,
    label = null,
    className = null,
    after = null,
    callback = image => console.log(image),
    meta: { error, touched },
    ...field
}) {
    // const { t } = useTranslation();
    const isValid = !(error && touched);
    const errorClass = !isValid ? 'invalid' : '';
    const inputClass = className ? className : '';
    // const [fileName, setFileName] = useState(t('choose_photo'));
    
    return (
        <div className={`form-group custom-file ${inputClass} ${errorClass}`}>
            {label &&
                <span className="form-label">{label}</span>}
            {hint &&
                <span className="form-hint">{hint}</span>}
            <input
                {...input}
                {...field}
                value={field.value || ''}
                className="custom-file-input"
                type="file"
                accept="image/x-png,image/png,image/jpg,image/jpeg"
                // onChange={e => {
                //     // e.preventDefault();
                //     // convert files to an array
                //     const files = [...e.target.files];
                //     const file = files[0];
                //     if (file.name) setFileName(file.name);
                //     const reader = new FileReader();
                //     reader.readAsDataURL(file);
                //     reader.onloadend = () => { callback(reader.result)};
                //     reader.onerror = (err) => console.log(err);
                //     input.onChange(e);
                // }
                // }
            />
            <label className="custom-file-label">{label}</label>
            {after &&
                <div className="form-after">{after}</div>}
            {!isValid &&
                <div className="form-error">{error}</div>}
        </div>
    )
}
